export default {
	data() {
		return {
			searchKey: [],
			ipt: '',
			searchClose: true,

		}
	},
	onShow() {
		var vv = uni.getStorageSync('searchLocal');
		var arr = vv.split("-");
		this.searchKey = arr;
	},
	methods: {

		clearKey: function() {
			var that = this;
			uni.showModal({
				title: '提示',
				content: '点击确定将删除所有历史信息，确定删除吗？',
				success: function(res) {
					if (res.confirm) {
						that.searchKey = [];
						uni.setStorage({
							key: 'searchLocal',
							data: that.searchKey
						});
						
					} else if (res.cancel) {

					}å
				}
			});

		},
		searchNow: function(e) {
			if (this.ipt == '') {
				uni.showToast({
					title: '未输入搜索关键字',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			var that = this;
			var newArr = that.searchKey;
			newArr.push(this.ipt);
			this.searchKey = newArr;
			var newStr = newArr.join('-');
			uni.setStorage({
				key: 'searchLocal',
				data: newStr
			});
		}
	}
}
